.EmptyNotifications{
    border: 0px solid red;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    & .icon{
      margin-bottom: 10px;
      & svg{
        font-size: 5em;
      }
    }
    & .primary{
      font-size: 1.5em;
    }  
}
